@charset "UTF-8";
/*
 * CSS Reset
 * @Reference: Normalize.css: https://github.com/necolas/normalize.css
 * @Idea: reset 的目的不是让默认样式在所有浏览器下一致，而是减少默认样式有可能带来的问题。
 * @Author: xiaoming.bai
 * @Date: 2019-03-05 10:33:59
 * @Last Modified by: xiaoming.bai
 * @Last Modified time: 2019-06-15 13:36:18
 */
html {
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin-left: auto !important;
  margin-right: auto !important;
  min-width: 320px;
  max-width: 1080px;
  color: #333;
  background-color: #fff;
  font: 14px/1.5 "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom); }

body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, pre,
dl, dt, dd, ul, ol, li, form, fieldset, lengend, button,
input, textarea, th, td {
  margin: 0;
  padding: 0; }

article, aside, details, figcaption, figure, main,
footer, header, hgroup, menu, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block;
  *display: inline;
  *zoom: 1; }

h1 {
  font-size: 2em; }

h2 {
  font-size: 1.5em; }

h3 {
  font-size: 1.17em; }

h4 {
  font-size: 1em; }

h5 {
  font-size: 0.83em; }

h6 {
  font-size: 0.67em; }

small {
  font-size: 80%; }

a {
  color: #409eff;
  background-color: transparent;
  text-decoration: none; }

a:focus, a:hover {
  color: #66b1ff;
  outline: 0; }

a:active {
  color: #3a8ee6;
  outline: 0; }

input, button {
  border: 0;
  outline: none; }

img {
  border: 0;
  -ms-interpolation-mode: bicubic; }

ol, ul {
  list-style: none; }

b, strong {
  font-weight: bolder; }

mark {
  background-color: #ff0;
  color: #000; }

legend {
  color: #000; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

pre, code, kbd, samp {
  font-family: monospace, serif;
  _font-family: 'courier new', monospace;
  font-size: 1em; }

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word; }

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none; }
