.part {
  margin: 20px;
  padding: 14px;
  background-color: #fff;
  border-radius: 12px;
  -webkit-box-shadow: 0 8px 16px 0 rgba(7, 17, 27, 0.1);
          box-shadow: 0 8px 16px 0 rgba(7, 17, 27, 0.1);
}
.part-title {
  margin: 0 auto;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 20px;
  background-color: #86ff00;
  border-radius: 20px;
}
