.transform-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.flexbox-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.grid-center {
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.hide {
  display: none; }

.show {
  display: block; }

.inline-block {
  display: inline-block; }

.visible {
  visibility: visible; }

.invisible {
  visibility: hidden; }

.fl {
  float: left;
  *display: inline;
  _display: inline; }

.fr {
  float: right;
  *display: inline;
  _display: inline; }

.clearfix::after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
  height: 0; }

.clearfix {
  *zoom: 1; }

.ellipsis {
  white-space: nowrap;
  word-wrap: break-word;
  word-break: break-all;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden; }

.unselectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.uncursor {
  cursor: none; }

.unoutline {
  outline: none; }

.unappearance {
  -webkit-appearance: none; }

.remove-tap-highlight {
  -webkit-tap-highlight-color: transparent; }

.notallowed {
  cursor: not-allowed; }

.unevents {
  pointer-events: none; }

.offscreen {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.offscreen2 {
  position: absolute;
  left: -9999px; }

.hide-text {
  text-indent: -9999px;
  overflow: hidden;
  text-align: left; }

.transparent-text {
  font: 0/0 serif;
  color: transparent;
  background-color: transparent;
  text-shadow: none;
  border: 0; }

.retext-x {
  direction: rtl;
  unicode-bidi: bidi-override; }

.retext-y {
  -webkit-writing-mode: tb-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl; }

.extend-hotspot {
  position: relative; }
  .extend-hotspot:before {
    content: '';
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px; }

.safe-area-inset {
  display: block;
  background: #fff;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom); }

.fade {
  opacity: 0;
  filter: alpha(opacity=0) \9;
  -webkit-transition: opacity .15s linear;
  transition: opacity .15s linear; }

.fade.in {
  opacity: 1;
  filter: alpha(opacity=100) \9 ; }
